#pageloader {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #fff;
  transition: 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
}

body {
  letter-spacing: 0.01em;
  height: 100vh;
  line-height: 1.9;
  overflow: hidden;
  overflow-y: auto;
}
body.nav-open, body:not(.loaded) {
  overflow: hidden;
}
body.loaded #pageloader {
  opacity: 0;
  pointer-events: none;
}

h1 .h3,
.h1 .h3 {
  display: block;
  letter-spacing: 0.04em;
}
h1 + .btn-secondary,
.h1 + .btn-secondary {
  margin-top: 4rem;
}

p + .btn-secondary {
  margin-top: 3rem;
}

a {
  text-decoration: none;
  transition: 0.3s;
}
a .icon {
  color: #7bc5a2;
  margin-right: 1.2rem;
  font-size: 1.8rem;
}

.btn-secondary {
  text-transform: uppercase;
  display: inline-block;
  border: 2px solid #687dd8;
  color: #fff;
  padding: 1.25rem 3rem;
  border-radius: 0;
  white-space: nowrap;
}
.btn-secondary:hover {
  color: #fff;
}

.dark * {
  color: #fff !important;
}
.dark .btn-secondary {
  border: 2px solid #fff;
  color: #fff;
}

.stencil {
  position: relative;
  padding-bottom: 0.5rem;
}
.stencil::after {
  content: "";
  position: absolute;
  height: 2px;
  width: 2.2rem;
  background: #7bc5a2;
  top: 100%;
  left: 0;
}
.stencil:not(.stencil-centered) + p {
  margin-top: 3rem;
}
.stencil.stencil-centered {
  text-align: center;
}
.stencil.stencil-centered::after {
  left: 50%;
  transform: translateX(-50%);
}
.dark .stencil::after {
  background: #fff;
}

.progress-bar {
  width: 0%;
}

.list-toggler.slidable {
  position: relative;
}
.list-toggler.slidable::after {
  content: "\e604";
  font-family: "simple-line-icons";
  margin-left: 0.7rem;
  font-size: 1.3rem;
  transition: 0.3s;
  display: inline-block;
}
.list-toggler.slidable.active::after {
  transform: rotate(-180deg);
}

