.custom-radio {
  display: block;
  position: relative;
  padding-left: 3rem;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.custom-radio .checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 2.1rem;
  width: 2.1rem;
  border: 2px solid #58b589;
  border-radius: 50%;
}
.custom-radio .checkmark:after {
  content: "";
  position: absolute;
  left: 0.7rem;
  top: 0.4rem;
  transform: translateX(-50%) translateY(-50%);
  width: 0.4rem;
  height: 0.8rem;
  border: solid #fff;
  border-width: 0 1px 1px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  display: none;
}
.custom-radio input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.custom-radio input:checked ~ .checkmark {
  background-color: #58b589;
}
.custom-radio input:checked ~ .checkmark:after {
  display: block;
}

