.about-sec .carousel-inner .carousel-item.active,
.about-sec .carousel-inner .carousel-item-next,
.about-sec .carousel-inner .carousel-item-prev {
  display: flex;
}
@media (min-width: 768px) {
  .about-sec .carousel-inner .carousel-item-end.active,
.about-sec .carousel-inner .carousel-item-next {
    transform: translateX(25%);
  }
  .about-sec .carousel-inner .carousel-item-start.active,
.about-sec .carousel-inner .carousel-item-prev {
    transform: translateX(-25%);
  }
}
.about-sec .carousel-inner .carousel-item-end,
.about-sec .carousel-inner .carousel-item-start {
  transform: translateX(0);
}
.about-sec .carousel-control-prev,
.about-sec .carousel-control-next {
  width: auto;
  height: calc(50% - 0.5rem);
  width: 5.8%;
  opacity: 1;
  transition: 0.3s;
}
.about-sec .carousel-control-prev:hover,
.about-sec .carousel-control-next:hover {
  box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.19);
}
@media (max-width: 575.98px) {
  .about-sec .carousel-control-prev,
.about-sec .carousel-control-next {
    width: 25%;
  }
}
.about-sec .carousel-control-prev {
  left: inherit;
  right: 0;
  bottom: inherit;
  background: #91a0e2;
}
.about-sec .carousel-control-next {
  top: inherit;
  background: loghten(#687dd8, 10%);
}

.events-sec .event-list {
  display: grid;
  grid-template-columns: 1fr 0.5fr 0.5fr;
  grid-gap: 3rem;
  position: relative;
  margin-bottom: 3.5rem;
}
@media (max-width: 575.98px) {
  .events-sec .event-list {
    display: block;
  }
}
.events-sec .event-list::after {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: calc(50% - 1.5rem);
  height: 1px;
  background: #d3d5d1;
  opacity: 0.5;
}
@media (max-width: 575.98px) {
  .events-sec .event-list .event + .event {
    margin-top: 3rem;
  }
}
.events-sec .event-list .event:nth-child(2) {
  grid-column: 1;
  grid-row: 2;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.events-sec .event-list .event:nth-child(3) {
  grid-column: 2/span 2;
  grid-row: 1;
}
.events-sec .event-list .event:not(.dark) .date {
  color: #7bc5a2;
  text-shadow: 0 0;
}
.events-sec .event-list .event.dark {
  background: #7bc5a2;
  text-align: center;
  padding: 3.5rem 3.5rem 4.3rem;
  position: relative;
}
.events-sec .event-list .event.dark p {
  max-width: 37.4rem;
  margin: auto;
}
.events-sec .event-list .event.dark .event-title {
  padding-bottom: 2rem;
}
.events-sec .event-list .event.dark .date {
  font-size: 4.8rem;
  font-family: "Rufina", sans-serif;
  color: #fff;
  background: #687dd8;
  position: absolute;
  top: 0;
  left: 0;
  line-height: 0.5;
  padding: 1rem;
}
.events-sec .event-list .event.dark .date span {
  font-size: 1.4rem;
  font-family: "Sintony", sans-serif;
  display: block;
  margin-top: 1.3rem;
}
.events-sec .event-list .event.dark.bg-image {
  display: flex;
  align-items: flex-end;
  padding: 43.45% 2rem 0rem;
  background-size: cover;
  background-position: center;
  position: relative;
  overflow: hidden;
  z-index: 1;
}
.events-sec .event-list .event.dark.bg-image::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.3;
  z-index: -1;
}
.events-sec .event-list .event.dark p {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}